.header{
    margin-top: 30px;
    margin-bottom: 20px;
    display: flex;
    justify-content: space-around;
    .mark{
        display: flex;
        img{
            width: 100px;
            height: 100px;
            margin-right: 30px;
            border-radius: 50%;
        }
        h1{
            display: flex;
            justify-content: center;
            align-items: center;
            font-size: 35px;
            a{
                text-decoration: none;
                color: black;
                font-weight: bold;
                font-style: italic;
            }
        }
    }
    .menu{
        // margin-top: 10px;
        display: flex;
        align-items: center;
        li{
            margin-right: 30px;
            a{
                text-decoration: none;
                color: black;
                font-size: 20px;
            }
        }
    }
}



.container-pro{
    .slick{
        img{
            height: 85vh;
        }
    }
    h1{
        text-align: center;
        font-size: 40px;
        margin-top: 50px;
        margin-bottom: 50px;
    }
    section{
        .clothes{
            display: flex;
            justify-content: space-around;
            flex-wrap: wrap;
            .box{
                transition: 0.5s;
                border: 2px solid gray;
                margin-bottom: 100px;
                img{
                    height: 300px;
                    width: 300px;
                }
                .price{
                    height: 150px;
                    display: flex;
                    flex-direction: column;
                    justify-content: flex-end;
                    align-items: flex-end;
                    margin-bottom: 30px;
                    margin-right: 20px;
                }
            }
            .box:hover{
                box-shadow: 5px 5px 5px rgb(103, 103, 103);
                transition: 0.5s;
            }
        }
        ul{
            height: 100px;
            display: flex;
            justify-content: flex-end;
            margin-right: 100px;
            li{
                margin-left: 30px;
                margin-bottom: 30px;
                a{
                    font-size: 30px;
                    color: gray;
                    i{
                        transition: 0.5s;
                    }
                }
                i:hover{
                    transition: 0.5s;
                }
                a .fa-facebook-square:hover{
                    color: blue;
                    font-size: 40px;
                    transition: 0.5s;
                }
                a .fa-instagram:hover{
                    color: pink;
                    font-size: 40px;
                }
                a .fa-twitter-square:hover{
                    color: aqua;
                    font-size: 40px;
                }

            }
        }
    }
    footer{
        background-color: black;
        height: 100px;
        display: flex;
        justify-content: center;
        align-items: center;
        p{
            color: white;
            font-size: 30px;
        }
    }
}


.under-line{
    border-bottom: 2px solid black;
    transition: width 1s ease-out;
    width: 10px;
}

.yen:hover+.under-line{
    width: 55px;
}

