.container{
    background-image: url(../../img/forest.jpg);
    height: 100vh;
    background-size: cover;
    .center{
        height: 100vh;
        display: flex;
        justify-content: center;
        align-items: center;
        .Collections{
            font-size: 50px;
            padding: 10px;
            color: white;
            text-decoration: none;
            position: relative;
            display: inline block;
            // border: 2px solid greenyellow;
            font-weight: bold;
            transition: all .2s;
            // outline: none;
            // transition: 1s;
        }
        .Collections::before,
        .Collections::after{
            position: absolute;
            z-index: 2;
            content: '';
            width: 0;
            height: 0;
            border: 2px solid transparent;
        }
        .Collections::before{
            top: -2px;
            right: -2px;
        }

        .Collections::after{
            bottom: -2px;
            left: -2px;
        }
        .Collections:hover::before,
        .Collections:hover::after{
            width: 100%;
            height: 100%;
        }
        .Collections:hover{
            color: black;
            transition: 1s;
        }
        .Collections:hover::before{
            border-radius: 30px;
            border-bottom-color: #3be5ae;
            border-left-color: #3be5ae;
            transition: height .3s, width .3s .3s;
        }
        .Collections:hover::after{
            border-radius: 30px;
            border-top-color: #3be5ae;
            border-right-color: #3be5ae;
            transition: height .3s, width .3s .3s;
        }
    }

}